import { Link } from 'react-router-dom';
import AutomaticIcon from '@/components/icons/car/AutomaticIcon';
import LocationIcon from '@/components/icons/car/LocationIcon';
import { Car } from '@/types/cars.types';
interface Props {
    car: Car;
}

const CarCard = ({ car }: Props) => {
    return (
        <div className="flex flex-col gap-2 shadow-[0px_2px_12px_0px_#14142B14] rounded-lg p-2">
            <div className="flex flex-row justify-between">
                <div className="inline-block">
                    <img className="h-4 w-4 inline-block me-1" src={car?.brand?.logo} alt="" />
                    <p className="inline-block text-white-blue-900 font-cairo font-medium text-[16px] leading-[20px] tracking-normal text-right">
                        {car.brand.name} {car.brand_model.name} {car.make_year}
                    </p>
                </div>
                <span className="text-white-blue-50 rounded-sm p-1 bg-[#22AD5C] font-cairo font-medium text-[12px] leading-[13.8px] tracking-normal">
                    {car.status}
                </span>
            </div>
            <div>
                <img className="w-[242px] h-[100px] object-contain" src={car.media.image} alt="" />
            </div>
            <div className="py-1 flex flex-row justify-between">
                <div className="flex flex-row items-center gap-[6px]">
                    {car.transmission_type === 'auto' && <AutomaticIcon />}
                    <p className="font-cairo font-medium text-primery-blue-900 text-[14px] leading-[16.8px] tracking-normal text-center">
                        {car.transmission_type}
                    </p>
                </div>
                <div className="flex flex-row items-center gap-1">
                    <LocationIcon />
                    <p className="font-cairo font-medium text-primery-blue-900 text-[14px] leading-[16.8px] tracking-normal text-center">
                        {/* {car.location} */}
                        السعودية
                    </p>
                </div>
            </div>
            <div className="flex flex-row justify-between items-center">
                <Link to={`/cars/${car.id}/edit`}>
                    <button className="rounded-lg hover:bg-white-blue-200 py-2 px-[43.5px] bg-white-blue-50 border border-white-blue-500 font-medium text-xs leading-[13.8px] tracking-normal text-white-blue-900">
                        تعديل
                    </button>
                </Link>
                <Link to={`/cars/add-booking?carID=${car.id}`}>
                    <button className="rounded-lg hover:bg-white-blue-200 py-2 px-[43.5px] bg-primery-blue-50 border border-primery-blue-50 shadow-[0px_1px_2px_0px_#1018280D] font-medium text-xs leading-[13.8px] tracking-normal text-primery-blue-500">
                        تأجير
                    </button>
                </Link>
            </div>
        </div>
    );
};

export default CarCard;
