import UnderlineHeading from '@/components/heading/UnderlineHeading';
import ArrowBackIcon from '@/components/icons/ArrowBackIcon';
import SearchIcon from '@/components/icons/Search';
import { Input } from '@/components/ui/input';
import CarListFilters from './CarListFilters';
import CarListComponent from '../../../../components/carsList/CarListComponent';
import { useEffect, useState } from 'react';
import { endpoints } from '@/lib/axios/axios-global';
import { apiGet } from '@/lib/axios/axios-utils';
import AddDropdown from '../addCar/AddDropdown';
import CarCard from './CarCard';

interface Props {
    CarListPagination: any;
}

const CarsList = ({ CarListPagination }: Props) => {
    const [cars, setCars] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [totalItems, setTotalItems] = useState(0);
    const [filters, setFilters] = useState({});
    const [search, setSearch] = useState('');

    useEffect(() => {
        const fetchCars = async () => {
            try {
                const response = await apiGet({
                    endpoint: endpoints.cars.pagination,
                    queryParams: { page: currentPage, ...filters, search },
                });

                setTotalItems(response.data.data.pagination.total);

                setCars(response.data.data.data);
            } catch (error) {
                console.error('Error fetching cars:', error);
            }
        };

        fetchCars();
    }, [currentPage, filters, search]);

    return (
        <div className="mb-10">
            <div className="flex flex-col p-2 sm:flex-row sm:items-center sm:justify-between">
                <UnderlineHeading text="قائمة السيارات" />
                <div className="flex flex-row items-center gap-4">
                    <div className="flex flex-row items-center w-[287px] border border-white-blue-300 bg-white-blue-50 h-[36px] p-2 rounded-[6px]">
                        <Input
                            placeholder="ابحث برقم السيارة أو الماركة أو الموديل ...."
                            className="text-white-blue-800 p-0 tracking-normal text-xs lg:text-sm lg:leading-[16.8px] border-none outline-none focus-visible:ring-0 placeholder:text-white-blue-800"
                            onChange={(e) => setSearch(e.target.value)}
                        />
                        <button className="bg-transparent border-none">
                            <SearchIcon className="text-white-blue-800" />
                        </button>
                    </div>
                    <div>
                        <AddDropdown />
                    </div>
                    <div className="flex items-center justify-center w-10 h-10 bg-primery-blue-50 border border-primery-blue-50 shadow-[0px_1px_2px_0px_#1018280D] rounded-lg ">
                        <ArrowBackIcon />
                    </div>
                </div>
            </div>
            <div className="mt-4">
                <CarListFilters setFilters={setFilters} />
                <div className="mt-6">
                    <CarListComponent cars={cars} CarCard={CarCard} />
                </div>
            </div>
            <div className="mt-4">
                <CarListPagination currentPage={currentPage} setCurrentPage={setCurrentPage} totalItems={totalItems} />
            </div>
        </div>
    );
};

export default CarsList;
