import ArrowDownIcon from '@/components/icons/ArrowDownIcon';
import ArrowUpIcon from '@/components/icons/ArrowUpIcon';
import {
    DropdownMenu,
    DropdownMenuContent,
    DropdownMenuItem,
    DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu';
import i18n from '@/lib/i18n/i18n';
import { useState } from 'react';
import { Link } from 'react-router-dom';

export default function AddDropdown() {
    const [isOpen, setIsOpen] = useState(false);

    return (
        <DropdownMenu onOpenChange={(open) => setIsOpen(open)} dir={i18n.dir()}>
            <DropdownMenuTrigger
                className="flex flex-row items-center justify-center gap-2 w-24 h-9 text-base leading-5 font-semibold font-cairo tracking-normal bg-primery-blue-700 text-white-blue-50 px-2 py-1 rounded-lg 
                          hover:bg-primery-blue-500 
                          focus:outline-none focus:ring-2 focus:ring-primery-blue-700 focus:ring-offset-2
                          disabled:bg-primery-blue-200 disabled:opacity-50"
            >
                إضافة
                {isOpen ? <ArrowUpIcon /> : <ArrowDownIcon />}
            </DropdownMenuTrigger>
            <DropdownMenuContent className="min-w-24">
                <DropdownMenuItem asChild>
                    <Link
                        to="/cars/add-car"
                        className="cursor-pointer px-4 py-3 text-white-blue-900 text-base leading-5 font-semibold font-cairo tracking-normal"
                    >
                        سيارة
                    </Link>
                </DropdownMenuItem>
                <DropdownMenuItem asChild>
                    <Link
                        to="/cars/add-fleet"
                        className="cursor-pointer px-4 py-3 text-white-blue-900 text-base leading-5 font-semibold font-cairo tracking-normal"
                    >
                        أسطول
                    </Link>
                </DropdownMenuItem>
            </DropdownMenuContent>
        </DropdownMenu>
    );
}
